<template>
  <div class="hello">
    <div class="page-top">
        <div class="page-content">
            <h2>任务计划列表</h2>
        </div>
    </div>
    <div class="main">
        <h3 class="big-title">添加任务：</h3>
        <input 
            :placeholder="placeholder_value"
            class="task-input" 
            type="text"
            v-model="inputtitle"
            @keyup.enter="inputenter"
        />
        <ul class="task-count" v-show="list.length">
            <li>{{isok}}个任务未完成</li>
            <li class="action">
                <router-link class="active" to ="#all" router_value="all">所有任务</router-link>
                <router-link to="#none" router_value="none">未完成的任务</router-link>
                <router-link to="#ok" router_value="ok">完成的任务</router-link>
            </li>
        </ul>
        <h3 class="big-title">任务列表：</h3>
        <div class="tasks">

            <span class="no-task-tip" v-show="!list.length">还没有添加任何任务</span>
            <ul class="todo-list">
                <li 
                    class="todo"  
                    :class="{completed:item.isOk,editing:item===copytitile}" 
                    v-for="(item,index) in active_change" :key="index" 
                    @dblclick="dbclick(item)">
                    
                    <div class="view">
                        
                        <input 
                            
                            class="toggle" 
                            type="checkbox" 
                            v-model="item.isOk"
                        />
                        <label>{{item.title}}</label>
                        <button class="destroy" @click="remove(item)"></button>
                    </div>
                    <input
                        v-foucs="item===copytitile" 
                        class="edit" 
                        type="text" 
                        v-model="edittitle"
                        @blur="input_blue(item)"
                        @keyup.enter="input_blue(item)"
                        @keyup.esc="input_esc(item)"
                    />
                </li>
            </ul>
        </div>
    </div>
  </div>
</template>

<script>
var list = [
	// {
	// 	title:"任务1",
	// 	isOk:false
	// },
	// {
	// 	title:"任务2",
	// 	isOk:false
	// }
];
export default {
  name: 'HelloWorld',
  props: {
    
  },
  data(){
    return{
      list:[],
      placeholder_value:"例如：吃饭睡觉打豆豆；    提示：+回车即可添加任务" ,
      inputtitle:"",
      copytitile:"",
      baochun:"",
      edittitle:"",
      active:"all"
    }
	},
	watch:{   
		$route( to , from ){
            this.active=this.$route.hash.substr(1,this.$route.hash.length-1);
            
        },
        list:function(){
            console.log("list");
            localStorage.setItem("Storage_list",JSON.stringify(this.list));
        },
	},
	methods:{
		inputenter(el){
			if(this.inputtitle!==""){
				this.list.push({
					title:this.inputtitle
                });
                console.log(this.list);
				this.inputtitle=""
			}else{
				return
			}
			
		},
		remove(item){
			let index=this.list.indexOf(item);
			this.list.splice(index,1);

		},
		dbclick(item){
			console.log(this.$route.query);
			this.baochun="";
			this.copytitile=item;
			this.baochun=item.title;
			this.edittitle=item.title;
			//console.log(item===this.copytitile);
		},
		input_blue(item){
			item.title=this.edittitle;
			this.copytitile=""
			//console.log(item,this.edittitle,this.list)
		},
		input_esc(item){
			console.log(this.baochun)
			item.title=this.baochun;
			this.edittitle=this.baochun;
		}
	},
	computed: {
        
		isok:function(){
            if(this.list.length){
                return this.list.filter(item=>{
				    return !item.isOk
			    }).length 
            }
			
        },
        active_change:function(){
            var active_list={
                 all(list){
                     return list
                 },
                 none(list){
                     return list.filter(data=>{
                         return !data.isOk
                     });
                 },
                 ok(list){
                     return list.filter(data=>{
                         return data.isOk
                     });
                 }
            }
            
            return active_list[this.active]?active_list[this.active](this.list):this.list
            console.log("active_change",this.list)
        }
	},
	directives: {
		"foucs":{
			update (el,binding) {
				if(binding.value){
					el.focus();
				}
			}
		},
		
    },
    mounted(){
        let store=localStorage.getItem("Storage_list");
        if(store){
            this.list=JSON.parse(store)
        }else{
            this.list=[]
        }
        console.log(this.list);
    }
}
</script>

